<template>
  <div id="home">
    <h1>{{currentText}}</h1>
    <h2>{{timetext}}</h2>
    <b-btn variant="info" class="m-1" v-if="status!==1" @click="start">
      <font-awesome-icon :icon="['fas','play']" ></font-awesome-icon>
    </b-btn>
    <b-btn variant="info" class="m-1" v-if="status==1" @click="pause">
      <font-awesome-icon :icon="['fas','pause']"></font-awesome-icon>
    </b-btn>
    <b-btn variant="info" class="m-1" v-if="current.length>0 || todos.length>0" @click="finish(true)">
      <font-awesome-icon :icon="['fas','step-forward']"></font-awesome-icon>
    </b-btn>
    <br>
    <img v-if="this.$store.state.isBreak==false" :src="'./img/cute-tomato.png'" class="tomato" >
    <img v-else :src="'./img/sleep_tomato.png'" class="tomato" >
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 0 = 停止
      // 1 = 播放
      // 2 = 暫停
      status: 0,
      timer: 0
    }
  },
  computed: {
    currentText () {
      return this.current.length > 0 ? this.current : this.todos.length > 0 ? '點擊開始' : '沒有事項'
    },
    timetext () {
      const m = Math.floor(this.timeleft / 60)
      const s = Math.floor(this.timeleft % 60)
      return `${m}:${s}`
    },
    alarm () {
      return this.$store.getters.alarm
    },
    timeleft () {
      return this.$store.getters.timeleft
    },
    current () {
      return this.$store.getters.current
    },
    todos () {
      return this.$store.getters.todos
    }
  },
  methods: {
    start () {
      if (this.status === 2) {
      // 暫停後繼續
        this.status = 1
        this.timer = setInterval(() => {
          this.$store.commit('countdown')
          if (this.timeleft <= 0) {
            this.finish(false)
          }
        }, 1000)
      } else {
      // 開始新倒數
        if (this.todos.length > 0) {
          this.$store.commit('start')
          this.status = 1
          this.timer = setInterval(() => {
            this.$store.commit('countdown')
            if (this.timeleft <= 0) {
              this.finish(false)
            }
          }, 1000)
        }
      }
    },
    finish (skip) {
      clearInterval(this.timer)
      this.status = 0
      this.$store.commit('finish')

      if (!skip) {
        const audio = new Audio()
        audio.src = './alarms/' + this.alarm
        audio.play()
      }
      if (this.todos.length > 0) {
        this.start()
      } else {
        alert('結束')
      }
    },
    pause () {
      clearInterval(this.timer)
      this.status = 2
    }
  }

}
</script>

<style lang="stylus">
#home
  h1
    color #753830
  h2
    color #753830
  .tomato
    width 250px
    margin 2rem
    position absolute
    top 35%
    left calc(50% - 125px)
    animation: hue-rotate 5s infinite alternate
    @keyframes hue-rotate
      50%{
        filter: hue-rotate(100deg);
      }
</style>
